import React, { useEffect, useRef, useState } from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-scroll'
import DarkModeSwitch from '../features/DarkModeSwitch'
import { useScrollPosition } from '../features/useScrollPosition'
export function AppHeader() {
  const isDark = useSelector((state) => state.featureModule.isDark)
  const containerRef = useRef()
  const [hideOnScroll, setHideOnScroll] = useState('')

  useScrollPosition(
    ({ prevPos, currPos }) => {
      if (currPos.y === 0) containerRef.current.classList.add('transparent')
      else containerRef.current.classList.remove('transparent')
      const isShow = currPos.y > prevPos.y
      if (isShow) setHideOnScroll('hide')
      else setHideOnScroll('')
    },
    [hideOnScroll]
  )

  useEffect(() => {
    if (isDark) containerRef.current.classList.add('night')
    else containerRef.current.classList.remove('night')
  })

  return (
    <header
      ref={containerRef}
      className={`flex column space-around align-center header-container ${hideOnScroll} transparent`}
    >
      <section className="flex align-center header-logo">
        <span>
          <div className="logo"></div>
        </span>
      </section>
      <nav className='flex'>
        <ul className="clean-list flex">
          {['projects', 'about'].map((item) => 
            <li key={item} className="flex column align-center">
              <div className='dot'></div>
              <Link
                to={item}
                spy={true}
                smooth={true}
                offset={-170}
                duration={350}
                className="nav-link"
              >
                {item}
              </Link>
            </li>
          )}
        </ul>
        <DarkModeSwitch />
      </nav>
    </header>
  )
}
